*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    scroll-behavior:smooth;
}
.bg
{
    background: url(../Banner3.png);
    height: 710px;
margin-top: -60px; 
}

.navbar{

    background:#020413;
    width: 100%;

    .search{
        
        position: relative;
        
    
        &:hover .form-control{
            width: 200px;
            background: white;
            @media (min-width:390px) and (max-width:720px) {
            width: 100%;
             
            }
        }
        &:hover .btn{
            background: white;
            margin-left: 10px;
            color: #ff4545;
        }
        
        .form-control{
            background: transparent;
            width: 0px;
        text-align: left;
        padding-left: 40px;
            border-radius: 40px;
            top: -18px;
            left: 0px;
            transition: 0.9s;
            outline: none;
            border: none;
            box-sizing: border-box;
            box-shadow: none;
            
        }
        .btn{
            width: 36px;
            height: 36px;
            position: absolute;
            top: 0px;
            left: -10px;
            border-radius: 36px;
            @media (min-width:390px) and (max-width:720px) {
            background: white;
             
            }
        }

        
    }
    .social-media{
        
        i{
            padding: 10px;
            transition: 0.9s;
            &:hover{
                color: #ff4545;
                transform:scale(1.2,1.2);
            }
        }
    .btn{padding: 10px;
    
        i{
            padding: 0px;
            
        }
    }
        
    }


    .navbar-toggler-icon{
        border-radius: 4px;
    }
    
        
}
.sticky-nav{
    background:#0A0C18;
    position: fixed;
    top: 0;
    left: 0; transition: 0.9s;
    animation: ss 0.9s;
}
@keyframes ss{
    0%{transform: translateY(-100%);}
    100%{transform: translateY(0%);}
    
}
.navbar-expand-lg .navbar-nav .nav-link{
    color: white;
    font-size: 15px;
    font-weight: 400;
    padding: 5px 20px;
    &:hover{
        color: #ff4545;
    }
}

.bb{
    img{
        background-repeat: no-repeat;
        width: 100%;
        height: 100%;
        background-size: cover;
        font-family: 'Roboto', sans-serif;
      
    }
}

